import React, { useState, useEffect, useCallback } from 'react';
import { Link } from 'gatsby';

import HeaderNav from './HeaderNav';
import ResolveLogo from './icons/ResolveLogo';
import Hamburger from './icons/Hamburger';
import ChevronArrow from './icons/ChevronArrow';
import DiagonalArrowRight from './icons/DiagonalArrowRight';
import Button from './Button';

import { screen, colors } from '../constants';

function renderBuyerLink(desktop = false) {
  return (
    <Link
      to="https://app.resolvepay.com"
      activeClassName="block-header__link_active"
      className={`block-header__link block-header__link_${
        desktop ? 'desktop' : 'mobile'
      } link_pink`}
      style={{ padding: '10px 0' }}
    >
      <div style={{ position: 'relative' }}>
        I'm a buyer
        <DiagonalArrowRight style={{ marginLeft: '4px' }} />
      </div>
    </Link>
  );
}

function LeftLinks({ links = [], showNavigationDrawer, desktop }) {
  const [overItemTitle, setOverItemTitle] = useState(null);

  return (
    <>
      {links.map(({ href, activeClassName, className, title, content }) => {
        const linkColor = !overItemTitle
          ? colors.darkSlateBlue
          : overItemTitle === title
          ? colors.darkSlateBlue
          : colors.baliHai;
        return (
          <HeaderNav
            key={title}
            content={content}
            showNavigationDrawer={showNavigationDrawer}
            onMouseOver={() => setOverItemTitle(title)}
            onMouseOut={() => setOverItemTitle(null)}
            onFocus={() => setOverItemTitle(title)}
            onBlur={() => setOverItemTitle(null)}
          >
            <Link
              to={href}
              activeClassName="block-header__link_active"
              className={`block-header__link
              block-header__link_${desktop ? 'desktop' : 'mobile'}
              ${content ? 'block-header__link_dropdown' : ''}
            `}
              style={{
                color: linkColor,
              }}
            >
              {title}
              {content && (
                <ChevronArrow
                  style={{
                    marginLeft: '5px',
                    position: 'relative',
                    top: '2px',
                  }}
                  color={linkColor}
                />
              )}
            </Link>
          </HeaderNav>
        );
      })}
    </>
  );
}

function renderRightButton(desktop = false, title, onClick) {
  return (
    <Button
      primary
      className={`block-header__link block-header__link_${
        desktop ? 'desktop' : 'mobile'
      } block-header__button`}
      onClick={onClick}
    >
      {title}
    </Button>
  );
}

function renderNavigationDrawer({
  links,
  showNavigationDrawer,
  isBuyerPage,
  style = {},
  onClickGetDemo,
}) {
  return (
    <div
      className={`block-header__drawer ${
        showNavigationDrawer ? 'block-header__drawer_expanded' : ''
      }`}
      style={style}
    >
      <LeftLinks links={links} showNavigationDrawer={showNavigationDrawer} />
      <Link
        to="https://app.resolvepay.com"
        activeClassName="block-header__link_active"
        className="block-header__link block-header__link_mobile"
        style={{ padding: '10px 0' }}
      >
        Log In
      </Link>
      {renderBuyerLink()}
      {!isBuyerPage && renderRightButton(true, 'Contact Sales', null)}
    </div>
  );
}

export default ({ links = [], style }) => {
  const [mobileView, setMobileView] = useState(false);
  const [showNavigationDrawer, setShowNavigationDrawer] = useState(false);

  const onResizeWindow = useCallback(() => {
    const nextMobileView = window.innerWidth < screen.tabletMax;

    if (showNavigationDrawer && mobileView && !nextMobileView) {
      setShowNavigationDrawer(false);
    }

    setMobileView(nextMobileView);
  }, [showNavigationDrawer, mobileView]);

  useEffect(() => {
    setMobileView(window.innerWidth < screen.tabletMax);

    window.addEventListener('resize', onResizeWindow);
    return () => window.removeEventListener('resize', onResizeWindow);
  }, [onResizeWindow]);
  const isBuyerPage = false; // router.pathname === '/buyers'

  function onClickGetDemo() {
    console.log('onClickDemo');
  }

  function onToggleNavigationDrawer() {
    setShowNavigationDrawer(!showNavigationDrawer);
  }

  return (
    <header className="block-header">
      <nav className="block-header__nav">
        <div className="block-header__links">
          <Link to="/" className="block-header__logo">
            <ResolveLogo onClick={() => console.log(`Router.push(' / ')`)} />
          </Link>
          {!mobileView && <LeftLinks links={links} desktop={true} />}
        </div>
        <div className="block-header__links">
          {renderNavigationDrawer({
            links,
            showNavigationDrawer,
            isBuyerPage,
            style,
            onClickGetDemo,
          })}
          <div
            className="block-header__hamburger"
            onClick={onToggleNavigationDrawer}
            onKeyDown={onToggleNavigationDrawer}
            role="button"
            tabIndex={0}
          >
            <Hamburger expanded={showNavigationDrawer} />
          </div>
          {renderBuyerLink(true)}
          <Link
            to="https://app.resolvepay.com"
            activeClassName="block-header__link_active"
            className="block-header__link block-header__link_desktop"
          >
            Log In
          </Link>
          {!isBuyerPage &&
            renderRightButton(true, 'Contact Sales', onClickGetDemo)}
        </div>
      </nav>
      <style jsx global>{`
        .block-header {
          margin-top: 22px;
        }
        .block-header__nav {
          display: flex;
          align-items: center;
          justify-content: space-between;
          min-height: 50px;
        }
        .block-header__links {
          display: flex;
          align-items: center;
        }

        @media (max-width: ${screen.tabletMax}px) {
          .block-header {
            margin-top: 0;
          }
        }
      `}</style>
      <style jsx global>{`
        .components__header-link {
          text-decoration: none;
        }
        .components__title {
          margin-top: 160px;
          padding: 20px 0px;
          font-size: 35px;
          margin-bottom: 30px;
          color: #999;
          border-bottom: 1px solid #999;
        }
        .components__title:first-child {
          margin-top: 0;
        }

        .components__section {
          margin-top: 30px;

          text-transform: uppercase;

          font-family: HalyardDisplay;
          font-size: 12px;
          font-weight: normal;
          line-height: 1.43;
          letter-spacing: 0.5px;

          color: #8f99ae;
        }

        .components__header-link {
          display: flex;

          margin-top: 20px;

          font-family: HalyardDisplay;
          font-size: 17px;
          font-weight: normal;
          line-height: 1.41;
          letter-spacing: 0.6px;
        }

        .components__header-link:first-child {
          margin-top: 12px;
        }

        .components__header-sublink {
          font-family: HalyardDisplay;
          font-size: 14px;
          font-weight: normal;
          line-height: 1.43;
          letter-spacing: 0.5px;
        }

        .block-header__logo {
          margin-right: 22px;
          position: relative;
          z-index: 2;
        }

        .block-header__link {
          margin-left: 48px;
          font-size: 17px;
          font-weight: normal;
          line-height: 1.41;
          letter-spacing: 0.6px;
        }

        .block-header__link_desktop:last-child {
          margin-right: 0;
        }

        .block-header__link_active {
          color: ${colors.activeLink};
        }

        .block-header__link_dropdown {
          display: flex;
          align-items: center;
        }

        .block-header__button {
          line-height: 53px !important;
          height: 52px;
          font-size: 17px !important;
          padding: 0 20px 0 23.5px;
          overflow: hidden;
        }

        .block-header__hamburger {
          display: none;
          user-select: none;
          z-index: 100;
        }

        .block-header__hamburger:hover {
          cursor: pointer;
        }

        .block-header__drawer {
          position: absolute;
          left: 100%;
          right: 0;
          top: 0;
          bottom: 0;

          display: flex;
          flex-direction: column;

          background-color: ${colors.secondary};
          overflow: hidden;

          box-shadow: 0 3px 6px rgba(0, 0, 0, 0.25351);
          z-index: 1;
          transition: 0.3s;
        }

        .block-header__drawer_expanded {
          left: 0;
        }

        @media (max-width: ${screen.tabletMax}px) {
          .block-header__nav {
            margin-top: 15px;
          }

          .block-header__link_desktop {
            display: none;
          }

          .block-header__hamburger {
            display: block;
          }
        }

        @media (max-width: ${screen.mobileMax}px) {
          .block-header__nav {
            margin-top: 8px;
          }
        }
      `}</style>
    </header>
  );
};
